export default {
	data() {
		return {
			title: 'Hello',
			expertList: []
		};
	},
	onLoad() {
		let _this = this;
		this.$ajax
			.get({
				url: '/user/listMaster?status=0&masterId=0&pageNum=0&pageSize=0'
			})
			.then(res => {
				_this.expertList = res.data.data;
				// console.log(_this.actions);
			});
	},
	methods: {}
};
